html {
  height: 100%;
}

body {
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
}

img {
  max-width: 100%;
  object-fit: contain;
  display: block;
}

.main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.container {
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1150px;
    box-sizing: border-box;
  }
}

.main {
  padding-top: 20px;
  padding-bottom: 80px;
}

.calc__title {
  font-size: 40px;
}

.calc__list {
  font-size: 26px;
  border: 2px solid #808080;
  padding: 10px 30px;
  max-width: 100px;
}

.calc-form__container {
  font-size: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.calc-form__set {
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.calc-form__title {
  font-size: 28px;
}

.calc-form__field-wrapper {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 20px;
}

.calc-form__input {
  font-size: 30px;
}

.calc-form__block-total {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 20px;
}

.calc-form__total {
  grid-column: 2/3;
  grid-row: 1/2;
}